/* Styles specific to the work section */
.work {
  background-color: scale-color(color(orange), $lightness: 60%);
  background-image: url("images/noise-texture.png");

  h2,
  h3,
  p {
    @extend %narrow-section;
  }

  a {
    color: color(red);

    &:hover,
    &:focus {
      color: color(violet);
    }

    &:focus {
      outline-color: color(violet);
    }
  }
}
