/* Styles specific to the resume section */
.print-resume {
  flex-direction: column; }

.resume > section {
  max-width: 560px; }
  @media (min-width: 768px) {
    .resume > section.experience {
      max-width: 900px; } }

.resume h3:before {
  border-top: 1vmin dotted black;
  content: "";
  display: block;
  margin: calc(1rem + 3vmin) auto;
  opacity: 0.7;
  width: 5vmin; }

.resume article {
  border-top: 3px solid rgba(0, 0, 0, 0.05);
  margin: 0 -1em;
  padding: 1em;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease; }
  .resume article:first-of-type {
    border-top: 0 none; }
  .resume article:hover {
    background-color: rgba(0, 0, 0, 0.05);
    border-color: transparent; }
  .resume article h3,
  .resume article h4,
  .resume article h5,
