  @media (min-width: 768px) {
    .resume article {
      display: grid;
      grid-template-columns: 34% 62%;
      grid-template-rows: auto auto 1fr;
      grid-column-gap: 4%; }
      .resume article p:not(.date) {
        grid-column: 2;
        grid-row: 1 / 4;
        margin: 0; }
      .resume article .full-date {
        margin-bottom: 1em; }
      .resume article.matrix {
        grid-template-rows: -webkit-max-content -webkit-max-content auto -webkit-max-content auto;
        grid-template-rows: max-content max-content auto max-content auto; }
        .resume article.matrix p:not(.date) {
          grid-column: 2;
          grid-row: 1 / 4;
          margin-bottom: 1em; }
          .resume article.matrix p:not(.date) ~ p:not(.date) {
            grid-column: 2;
            grid-row: 4 / 6;
            margin-bottom: 0; } }

